import React, { Component } from 'react';

class CategorySelect extends Component {
    changeCategory = (e) => {
        const selectValue = e.target.value;
        if(selectValue === "All") window.location.href = "/#/projects/";
        else window.location.href = "/#/categories/" + selectValue;
    }
    render() {
        const { categoryList, category } = this.props;
        const options = categoryList.map(option => {
            return <option key={"select-"+option} value={option}>{option}</option>
        });
        return(
            <h3 className="lined-title"><span>
                <label htmlFor="category-select">Catrgory : </label>
                <select id="category-select" className="category-select" value={category} onChange={this.changeCategory}>
                    <option value="All">All</option>
                    {options}
                </select>
                </span>
            </h3>
        );
    }
}

export default CategorySelect;