import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class ProjectsList extends Component {

    handleMouseOver = (e) => {
        const prList = document.querySelectorAll(".pr-li");
        for(let i=0; i<prList.length; i++) {
            prList[i].style.opacity = "0.65";
        }
        e.currentTarget.style.opacity = "1";
    }

    handleMouseOut = (e) => {
        const prList = document.querySelectorAll(".pr-li");
        for(let i=0; i<prList.length; i++) {
            prList[i].style.opacity = "1";
        }
    }

    componentDidUpdate(prevProps) {
            console.log(this.props.category);
            const cLink = document.querySelectorAll(".category-link");
            for(let i=0; i<cLink.length; i++) {
                cLink[i].classList.remove("active");
                cLink[i].getAttribute('data-item') === this.props.category ? 
                    cLink[i].classList.add('active')
                    : cLink[i].classList.remove("active");
            }
    }

    render() {
        const { data, limitNum } = this.props;
        const list = data.slice(0).reverse().slice(0,limitNum).map((item) => {
            return <li className="col-lg-4 col-md-6 pr-li" key={item.id} onMouseOver={this.handleMouseOver} onMouseOut={this.handleMouseOut}>
                    <div className="projects-list__wrap">
                        <Link to={'/detail/'+item.id} className="item-link" aria-labelledby={'item-title-'+item.id} aria-describedby={'item-txt-'+item.id}>
                            <img className="thumbnail-img" src={require("../images/" + item.thumbnail)} alt={item.title + " 썸네일 이미지"}/>
                            <p className="hidden-txt">{item.title}</p>
                        </Link>
                        <nav className="category-wrap">
                            {item.categories.map((category,index) => {
                                return <Link to={"/categories/"+category} className="category-link" data-item={category} key={"category" + index}>{category}</Link>
                            })}
                        </nav>
                        <Link to={'/detail/'+item.id} className="item-link txt-link" tabIndex="-1">
                            <h4 className="item-title" id={'item-title-'+item.id}>{item.title}</h4>
                            <p className="item-txt" id={'item-txt-'+item.id}>{item.simpleDescription}</p>
                        </Link>
                    </div>
                  </li>;
        });
        return (
            <section className="ProjectsList page">
                <ul className="projects-list row">
                    {list}
                </ul>
            </section>
        );
    }
}

export default ProjectsList;