import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ProjectsList from './layout/ProjectsList';

class Home extends Component {
    componentDidMount() {
        document.querySelector(".wrapper").classList.add("home");
        setTimeout(function(){
            document.querySelector('.home-top').classList.add('active');
        },500);
        setTimeout(function(){
            document.querySelector('.btn.animate-show').classList.add('animated');
            document.querySelector('.home-top__title').classList.add('animated');
        },800);
        setTimeout(function(){
            document.querySelector('.home-top__txt').classList.add('animated');
        },950);
    }
    render() {
        const { data } = this.props;
        return (
            <section className="Home">
                <div className="container">
                        <div className="home-top row">
                            <div className="home-top__contents col-12">
                                <h2 className="home-top__title animate-slide">Publisher<br/>
                                    for Web &amp; Mobile
                                </h2>
                                <p className="home-top__txt animate-slide">웹 페이지 구성하는게 재밌어서 행복하게 일하고 있는 <br/> 웹 퍼블리셔 '고봉훈'이라고 합니다</p>
                                <Link to="/projects" className="btn animate-show">View My Projects</Link>
                            </div>
                        </div>
                        <div className="recent-projects projects-list">
                            <h3 className="lined-title"><span>Recent Projects</span></h3>
                            <ProjectsList data={data} limitNum="6"/>
                            <Link to="/projects" className="btn">View More</Link>
                        </div>
                </div>
            </section>
        );
    }
}

export default Home;