import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class ProjectsDetail extends Component {
    componentDidMount() {
        const hash = window.location.hash;
        this.props.onHashChange(hash);
    }
    componentDidUpdate(prevProps, prevState) {
        if(prevProps.category !== this.props.category) {
            const hash = window.location.hash;
            this.props.onHashChange(hash);
        }
        else return false;
    }
    isImageLoaded = () => {
        console.log("top-image-loaded!");     
    }
    render() {
        const { data } = this.props;
        /*const style = {
            "backgroundImage" : "url("+ require("./images/" + data.detailTopImage) + ")"
        };*/
        const detailImgs = data.detailImages.map((item, index) => {
            return <img src={item} alt="" key={"images-"+index}/>;
        });
        const categories = data.categories.map((item, index) => {
            return <Link to={"/categories/"+item} key={"category-"+index}>{"#"+item}</Link>
        });
        return(
            <section className="ProjectsDetail">
                <div className="ProjectsDetail-top__wrap">
                    <div className="container">
                        <div className="row project-top">
                            <div className="project-top__txt col-md-6 col-12">
                                <em>Project Name.</em>
                                <h2>{data.title}</h2>
                                <p>{data.simpleDescription}</p>
                                <div className="project-detail__categories">{categories}</div>
                                <a href={data.link} className="btn" target="_blank">홈페이지 바로가기</a>
                            </div>
                            <div className="project-top__img col-lg-6 offset-lg-0 col-10 offset-0">
                                <img src={data.detailTopImage} alt="" onLoad={this.isImageLoaded}/>
                            </div>
                        </div>
                    </div>
                    </div>
                    <div className="container">
                        <div className="project-content row">
                            <ul className="project-info col-lg-8 col-12 offset-lg-2">
                                <li>
                                    <div>클라이언트</div>
                                    <div>{data.client}</div>
                                </li>
                                <li>
                                    <div>출시일</div>
                                    <div>{data.releaseDate}</div>
                                </li>
                                <li>
                                    <div>참여율</div>
                                    <div>{data.rate}</div>
                                </li>
                            </ul>
                            <p className="col-lg-8 offset-lg-2 col-12 project-description">{data.description}</p>

                            <div className="col-12 detail-imgs">
                                {detailImgs}
                            </div>
                        </div>
                    </div>
            </section>
        );
    }
}

export default ProjectsDetail;