import React, { Component } from 'react';

class Contact extends Component {
    componentDidMount() {
        document.querySelector(".contact-modal__body a").focus();
    }
    deactiveContact = () => {
        this.props.onDeActiveContact(false);
    }

    handleFirstKeyDown = (e) => {
        if(e.shiftKey && e.keyCode === 9) {
            e.preventDefault();
            document.querySelectorAll(".contact-modal__ft .btn")[0].focus();
        }
    }
    handleLastKeyDown = (e) => {
        if(e.keyCode === 9 && !e.shiftKey) {
            e.preventDefault();
            document.querySelectorAll(".contact-modal__body a")[0].focus();
        }
    }

    render() {
        return(
            <section className="Contact" id="contactModal" role="dialog" aria-modal="true" aria-labelledby="modal-title">
                <div className="contact-dimmer" onClick={this.deactiveContact}></div>
                <div className="contact-modal">
                    <div className="contact-modal__hd">
                        <h3 id="modal-title">Contact</h3>
                    </div>
                    <div className="contact-modal__body" id="modal-txt">
                        <a href="mailto:fotrise2@gmail.com" aria-label="이메일 주소" onKeyDown={this.handleFirstKeyDown} onKeyPress={this.handleFirstKeyDown}>fotrise2@gmail.com</a>
                        <p tabIndex="0" aria-label="연락처">010-3303-9838</p>
                        <p tabIndex="0" aria-label="사는 곳">서울특별시 관악구 행운동</p>
                        <a href="https://github.com/BongHoonKo" aria-label="깃헙 주소">github.com/BongHoonKo</a>
                    </div>
                    <div className="contact-modal__ft">
                        <button className="btn" onClick={this.deactiveContact} onKeyDown={this.handleLastKeyDown} onKeyPress={this.handleLastKeyDown}>확인</button>
                    </div>
                </div>
            </section>
        );
    }
}

export default Contact;