import React, { Component } from 'react';
import ProjectsList from './layout/ProjectsList';
import CategorySelect from './layout/CategorySelect';

class Projects extends Component {
    componentDidMount() {
        const hash = window.location.hash;
        this.props.onHashChange(hash);
    }
    render() {
        const { data, categoryList } = this.props;
        return(
            <section className="Projects page">
                <div className="container">
                <CategorySelect category="All" categoryList={categoryList}/>
                <ProjectsList category="All" data={data} />
                </div>
            </section>
        );
    }
}

export default Projects;