import React, { Component } from 'react';
import ProjectsList from './ProjectsList';
import CategorySelect from './CategorySelect';

class CategoryList extends Component {
    componentDidMount() {
        const hash = window.location.hash;
        this.props.onHashChange(hash);
    }
    componentDidUpdate(prevProps, prevState) {
        if(prevProps.category !== this.props.category) {
            const hash = window.location.hash;
            this.props.onHashChange(hash);
        }
        else return false;
    }

    render() {
        const { data, category, categoryList } = this.props;
        const filteredData = [];
        for(let i=0; i<data.length; i++) {
            const values = Object.keys(data[i].categories).map(function(e) {
                return data[i].categories[e]
            });
            /*if(Object.values(data[i].categories).indexOf(category) > -1) {
                filteredData.push(data[i]);
            }*/
            if(values.indexOf(category) > -1) filteredData.push(data[i]);
        }
        return(
            <section className="CategoryList page">
                <div className="container">
                    <CategorySelect category={category} categoryList={categoryList}/>
                    <ProjectsList category={category} data={filteredData} />
                </div>
            </section>
        );
    }
}

export default CategoryList;