import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Header extends Component {
    state = {
        lessThan400 : false
    };
    componentDidMount() {
        window.addEventListener("scroll", function(){
            const scrollTop = window.scrollY;
            const header = document.querySelector('.Header');
            if(scrollTop > 30) header.style.boxShadow = "5px 5px 15px rgba(0,0,0,0.15)";
            else header.style.boxShadow = "none";
        });
    }
    activeContact = () => {
        this.props.onClickContact();
    }
    showMobileNav = () => {
        const $mobileNav = document.querySelector('.mobile-nav');
        $mobileNav.style.display = "flex";
        $mobileNav.setAttribute("aria-expanded",true);
        setTimeout(function(){
            $mobileNav.style.right = "0";
        },100);
    }
    closeMobileNav = () => {
        const $mobileNav = document.querySelector('.mobile-nav');
        $mobileNav.style.right = "-100%";
        $mobileNav.setAttribute("aria-expanded",false);
        setTimeout(function(){
            $mobileNav.style.display = "none";
        },300);
    }
    render() {
        return (
            <header className="Header">
                <div className="container">
                    <h1 className="logo">
                        <a href="/">
                            <img src={require("../images/my_logo.png")} alt="로고"/>
                        </a>
                    </h1>
                    <nav className="main-nav">
                        <ul className="main-nav__list">
                            <li><Link to="/about">About</Link></li>
                            <li><Link to="/projects">Projects</Link></li>
                            <li><a href="javascript:void(0)" onClick={this.activeContact} aria-haspopup="true" aria-controls="contactModal">Contact</a></li>
                        </ul>
                    </nav>
                    <button className="mobile-nav__btn" onClick={this.showMobileNav} aria-haspopup="true" aria-controls="mobileNav" aria-label="메뉴 열기"></button>
                    <nav className="mobile-nav" id="mobileNav" aria-expanded="false">
                        <button className="mobile-nav__close" onClick={this.closeMobileNav} aria-label="메뉴 닫기"></button>
                        <ul className="mobile-nav__list">
                            <li><Link to="/about">About</Link></li>
                            <li><Link to="/projects">Projects</Link></li>
                            <li><a href="javascript:void(0)" onClick={this.activeContact} aria-haspopup="true" aria-controls="contactModal">Contact</a></li>
                        </ul>
                    </nav>
                </div>
            </header>
        );
    }
}

export default Header;